@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@200;300;400;500;600;700&family=Playfair+Display:wght@500;600;700&display=swap');

body {
  font-family: oswald;
  color: #232323;
  background-color: #FBFBFB;
  /* height: 100vh; */
  margin: 0;
  padding: 0;
  background-repeat: no-repeat;
  background-size: 85%, auto;
  background-position: center center;
}

h2 {
  text-transform: uppercase;
  font-size: 32px;
  letter-spacing: -1px;
  margin: 0;
}

h3 {
  font-size: 24px;
  font-weight: 300;
  letter-spacing: -0.5px;
  margin: 0;
}

h4{
  font-weight: 300;
  font-size: 16px;
  text-transform: uppercase;
}

h5{
  margin: 0;
  text-transform: uppercase;
  font-size: 24px;
  line-height: 35px;
  letter-spacing: -1px;
}

a{
  text-decoration: none;
}

input{
  -webkit-box-sizing: border-box; /* Safari/Chrome, other WebKit */
  -moz-box-sizing: border-box;    /* Firefox, other Gecko */
  box-sizing: border-box;         /* Opera/IE 8+ */
  -webkit-box-shadow: inset 0px 4px 9px 0px rgba(153,153,153,0.1); 
  box-shadow: inset 0px 4px 9px 0px rgba(153,153,153,0.1);
  width: 100%;
  border-radius: 5px;
  padding: 13px 10px 13px 20px;
  font-size: 16px;
  font-family: oswald;
  font-weight: 400;
  color: #686868;
  border: 1px solid rgba(211, 211, 211, 0.25);
  outline: none;
}

a{
  color: #232323;
}
button{
  cursor: pointer;
}

#root{
  display: flex;
  justify-content: center;
}

#button-shadow{
  -webkit-box-shadow: 0px 4px 9px 0px rgba(35,35,35,0.15); 
  box-shadow: 0px 4px 9px 0px rgba(35,35,35,0.15);
}

::placeholder{
  color: #999999;
}

.full-width{
  width: 100%
}

.seventy-width{
  width: 70%
}

.thirty-width{
  width: 30%
}

.align-right{
  display: flex;
  justify-content: end;
}

.flex-column-center{
  display: flex;
  flex-direction: column;
  align-items: center;
}

.flex-space-between{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.login{
  width: 363px;
  padding-top: 200px;
  display: flex;
  flex-direction: column;
  /* align-items: center; */
}

.login-form{
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-bottom: 12px;
}

.main-divider{
  margin-top: 50px;
}

.new-blog-form{
  width: 1440px;
  display: flex;
  gap: 20px;
}

.lrg-btn{
  width: 100%;
  padding: 16px 50px 16px 50px;
  background-color: #FFA959;
  border-radius: 5px;
  font-size: 24px;
  line-height: 20px;
  font-family: oswald;
  font-weight: 500;
  text-transform: uppercase;
  border: none;
}

.sml-btn{
  width: 100%;
  padding: 7px 20px 7px 20px;
  background-color: #FFA959;
  border-radius: 5px;
  font-size: 14px;
  letter-spacing: 0px;
  line-height: 16px;
  font-family: oswald;
  text-transform: uppercase;
  border: none;
}

.logout-btn{
  font-family: oswald;
  font-size: 18px;
  width: auto;
  display: flex;
  align-items: center;
  gap: 5px;
  border: none;
  background-color: transparent;
}

.remove-btn{
  font-size: 12px;
  line-height: 10px;
  background-color: #FFF;
  color: #FF5959;
  border: 1px solid #FF5959;
  margin: 10px 0 0px 0;
}

.remove-btn:hover{
  background-color: #F7D6D6;
}

.white-bg{
  background-color: #FFF;
}

.add-blog-btn{
  width: auto;
}

.new-blog-button{
  width: 300px;
  margin-top: 45px;
}

.new-blog-cancel{
  width: auto;
  font-size: 16px;
  line-height: 10px;
  padding: 16px 24px 16px 24px;
  margin-top: 10px;
}

.blog-list{
  display: flex;
  flex-wrap: wrap;
  margin-top: 50px;
  justify-content: center;
  gap: 53px;
}

.closed-card{
  width: 320px;
  background-color: #FFF;
  padding: 20px;
  border-radius: 10px;
  box-sizing:border-box;
}

.card-user-info{
  font-family: playfair display;
  font-size: 14px;
  margin: 10px 0 10px 0;
}

.card-two-column{
  font-family: playfair display;
  font-size: 16px;
  gap: 10px;
}

.subtitle{
  margin: 20px 0 20px 0;
}

.pagination-container{
  display: flex;
  justify-content: center;
  margin: 50px;
}

.pagination-nav{
  display: flex;
  gap: 30px;
  align-items: center;
}

.pagination-numbers{
  display: flex;
  margin: 0%;
  padding: 0%;
  list-style-type: none;
  gap: 10px;
}

.page-item{
  width: 30px;
  height: 30px;
  border-radius: 100%;
}

.arrow{
  width: 30px;
  height: 30px;
  border-radius: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 100%;
}

.arrow:hover{
  border: 2px solid #DDD;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}

.page-link{
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 100%;
}

.page-link:hover{
  border: 2px solid #DDD;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}

.container{
  width: 1440px;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.header{
  padding-top: 30px;
  width: 100%;
}

.footer{
  width: 1440px;
  padding: 16px 0 42px 0;
  border-top: 1px solid #232323;
}

.login{
  min-height: 90%;
  margin-bottom: -160px;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
}